(function() {
    'use strict';

    angular
        .module('vmsFrontend')
        .factory('vmsErrorMessage', vmsErrorMessage);

    /** @ngInject */
    function vmsErrorMessage($log, fieldName) {
        var service = {
            getErrorMsg: getErrorMsg
        };

        return service;

        function getErrorMsg(errors) {
            $log.log('getErrorMsg');
            $log.log(errors);

            var errorMsgs = [];
            errors.forEach(function(error) {
                if (error.code == "used_field") {
                    errorMsgs.push("被使用的 " + fieldMsg(error));
                } else if (error.code == "missing_field") {
                    errorMsgs.push("未輸入的 " + fieldMsg(error));
                } else if (error.code == "not_enough_password_strength") {
                    errorMsgs.push("密碼強度不足");
                }
            });

            return errorMsgs.join(";");
        }

        function fieldMsg(error) {
            $log.log(error.fields);
            $log.log('== fieldName ==');
            $log.log(fieldName);

            var fields = error.fields;
            var fieldArray = [];

            fields.forEach(function(field) {
                fieldArray.push(fieldName[field]);
            });

            return fieldArray.join(", ");
        }
    }
})();
(function() {
    'use strict';

    angular
        .module('vmsFrontend')
        .factory('vmsClient', vmsClient);

    /** @ngInject */
    function vmsClient($log, Restangular) {
        var service = {
            register: register,
            login: login,
            logout: logout,
            emailVerification: emailVerification,
            getProfile: getProfile,
            refreshToken: refreshToken,
            deleteAccount: deleteAccount
        };

        return service;

        function register(volunteer, successCallback, failureCallback) {
            Restangular.all('register').post(volunteer).then(function(response) {
                successCallback(response);
            }, function(response) {
                failureCallback(response);
            });
        }

        function login(credentials, successCallback, failureCallback) {
            Restangular.all('auth').post(credentials).then(function(response) {
                successCallback(response);
            }, function(response) {
                failureCallback(response);
            });
        }

        function logout(successCallback, failureCallback) {
            Restangular.all('auth').remove().then(function(response) {
                successCallback(response);
            }, function(response) {
                failureCallback(response);
            });
        }

        function emailVerification(email, verification_token, successCallback, failureCallback) {
            Restangular.one('email_verification/' + email + '/' + verification_token)
                .get()
                .then(function(response) {
                    successCallback(response);
                }, function(response) {
                    failureCallback(response);
                });
        }

        function getProfile(successCallback, failureCallback) {
            Restangular.one('users/me')
                .get()
                .then(function(response) {
                    successCallback(response);
                }, function(response) {
                    failureCallback(response);
                });
        }

        function refreshToken(successCallback, failureCallback) {
            Restangular.all('auth/refresh_token').post()
                .then(function(response) {
                    successCallback(response);
                }, function(response) {
                    failureCallback(response);
                });
        }

        function deleteAccount(credentials, successCallback, failureCallback) {
            Restangular.all('users/me/delete')
                .post(credentials)
                .then(successCallback, failureCallback);
        }
    }
})();
