(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
    'use strict';

    angular
        .module('vmsFrontend')
        .factory('jwtLocalStorage', jwtLocalStorage);

    /** @ngInject */
    function jwtLocalStorage($log, localStorageService) {
        var tokenKey = 'jwt_token';
        var lastNameKey = 'lastName';
        var service = {
            set: set,
            get: get,
            remove: remove,
            tokenKeyExists: tokenKeyExists
        };

        return service;

        function set(token) {
            $log.debug(token);
            return localStorageService.set(tokenKey, token);
        }

        function get() {
            return localStorageService.get(tokenKey);
        }

        function remove() {
            return localStorageService.remove(tokenKey);
        }

        function setLastName(lastName) {
            return localStorageService.set(lastNameKey, lastName);
        }

        function getLastName() {
            $log.debug("getLastName");
            return localStorageService.get(lastNameKey);
        }

        function tokenKeyExists() {
            var lsKeys = localStorageService.keys();

            $log.debug(lsKeys);

            if (lsKeys.indexOf(tokenKey) != -1) {
                return true;
            }

            return false;
        }
    }
})();
(function() {
    'use strict';

    angular
        .module('vmsFrontend')
        .directive('formatYear', formatYear);

    /** @ngInject */
    function formatYear($filter, $log) {
        return {
            require: 'ngModel',
            link: function(scope, element, attrs, modelCtrl) {

                modelCtrl.$parsers.push(function(inputValue) {

                    var transformedInput = $filter('date')(inputValue, 'yyyy');

                    $log.debug('=== formatYear directive ===');
                    $log.debug(transformedInput);

                    if (transformedInput != inputValue) {
                        modelCtrl.$setViewValue(transformedInput);
                        modelCtrl.$render();
                    }

                    return transformedInput;
                });
            }
        };
    }
})();
(function() {
    'use strict';

    angular
        .module('vmsFrontend')
        .factory('authorization', authorization);

    /** @ngInject */
    function authorization($rootScope, $state, authPrinciple, $log) {
        var service = {
            authorize: authorize
        };

        return service;

        function authorize() {
            $log.debug('authorize');

            return authPrinciple.identity().then(function() {
                $log.debug("identity success");

                var isAuthenticated = authPrinciple.isAuthenticated();

                // TODO: If there is a RBAC mechanism, it will be add a role control

                // Check if the user was authenticated
                if ($rootScope.toState.data.needAuth) {
                    if (isAuthenticated) {
                        // success
                        $log.debug('Authenticated');
                        // Go to origin state
                        //$state.go($rootScope.toState.name);
                    } else {
                        // failure
                        $log.debug('Not authenticated');
                        $rootScope.returnToState = $rootScope.toState;
                        $rootScope.returnToStateParams = $rootScope.toStateParams;

                        // Go to login page
                        $state.go('login');
                    }
                }
            });
        }
    }
})();
(function() {
    'use strict';

    angular
        .module('vmsFrontend')
        .factory('authPrinciple', authPrinciple);

    /** @ngInject */
    function authPrinciple($q, vmsClient, jwtLocalStorage, $timeout, $log, $http) {
        var _identity = undefined;

        $log.debug("=== _identity ===");
        $log.debug(_identity);

        var _authenticated = false;
        var service = {
            isIdentityResolved: isIdentityResolved,
            isAuthenticated: isAuthenticated,
            authenticate: authenticate,
            refreshToken: refreshToken,
            identity: identity
        };

        return service;

        function isIdentityResolved() {
            $log.debug("=== authPrinciple.isIdentityResolved() ===");
            return angular.isDefined(_identity);
        }

        function isAuthenticated() {
            //$log.debug("=== authPrinciple.isAuthenticated() ===");
            return _authenticated;
        }

        function authenticate(identity) {
            $log.debug("=== authPrinciple.authenticate() ===");
            $log.debug("identity = " + identity);
            _identity = identity;
            _authenticated = identity != null;

            $log.debug("_authenticated = " + _authenticated + " in authenticate()");

            if (identity) {
                jwtLocalStorage.set(identity);
            } else {
                jwtLocalStorage.remove();
            }
        }

        function refreshToken() {
            $log.debug("=== refreshToken() ===");

            var refreshTokenDeferred = $q.defer();

            $timeout(function() {
                vmsClient.refreshToken(function(response) {
                    var refreshToken = response.headers('Authorization');

                    if (angular.isDefined(refreshToken) && refreshToken != null) {
                        // Set the refreshToken into local storage

                        var jwtToken = refreshToken.substring(7);

                        authenticate(jwtToken);

                        refreshTokenDeferred.resolve(jwtToken);                       
                    }
                }, function(response) {
                    authenticate(null);
                    refreshTokenDeferred.reject();
                });
            }, 500);

            return refreshTokenDeferred.promise;
        }

        function identity(force) {
            $log.debug("=== authPrinciple.identity() ===");
            var deferred = $q.defer();

            if (force === true) _identity = undefined;

            if (angular.isDefined(_identity)) {
                deferred.resolve(_identity);

                return deferred.promise;
            }

            // retrive the identity data from local storage
            $timeout(function() {
                if (jwtLocalStorage.tokenKeyExists()) {
                    _identity = jwtLocalStorage.get();
                    _authenticated = true;
                    deferred.resolve(_identity);
                } else {
                    _identity = null;
                    _authenticated = false;
                    deferred.resolve(_identity);
                }
            }, 500);

            return deferred.promise;
        }
    }
})();
