(function() {
    'use strict';

    angular
        .module('vmsFrontend')
        .controller('RegisterController', RegisterController);

    /** @ngInject */
    function RegisterController($log, $state, fieldName, defaultAvatarPath, vmsClient, vmsErrorMessage, authPrinciple, $scope, cities, localStorageService) {
        var vm = this;
        vm.cities = cities;

        $log.log('RegisterController');

        vm.showAvatar = defaultAvatarPath;

        vm.datePicker = {
            mode: 'year',
            dateOptions: {
                datepickerMode:"'year'",
                minMode:"'year'",
                minDate:"minDate",
                showWeeks:"false"
            },
            status: {
                opened: false
            },
            format: 'yyyy'
        }

        vm.register = function() {
            $log.debug('=== register ===');
            
            $log.debug(vm.volunteer);

            if (vm.showAvatar != defaultAvatarPath) {
                vm.volunteer.avatar = vm.showAvatar;
            } 
            
            vmsClient.register(vm.volunteer, function(response) {
                $log.debug('success');
                $log.debug(response);

                localStorageService.set('username', vm.volunteer.username);
                authPrinciple.authenticate(response.data.auth_access_token);

                $state.go('registerSuccess', {last_name: vm.volunteer.last_name, email: vm.volunteer.email});
            }, function(response) {
                $log.debug('error');
                $log.debug(response);

                if (response.status == 422) {
                    $log.error(response.data);

                    var errors = response.data.errors;
                    vm.errorMsg = vmsErrorMessage.getErrorMsg(errors);
                }
            });
        };

        vm.selectAvatar = function(event, fileReader, file, fileList, fileObjects, object) {
            var base64Image = "data:" + object.filetype + ';base64,' + object.base64
            vm.showAvatar = base64Image;
        };

        vm.openDatePicker = function($event) {
            $log.debug("click openDatePicker");
            $event.preventDefault();
            $event.stopPropagation();
            vm.datePicker.status.opened = true;
        }
    }
})();
(function() {
    'use strict';

    angular
        .module('vmsFrontend')
        .controller('LoginController', LoginController);

    /** @ngInject */
    function LoginController($log, $location, vmsClient, authPrinciple, $state, $rootScope, localStorageService) {
        var vm = this;

        vm.login = function() {
            vmsClient.login(vm.credentials, function(response) {
                $log.debug('login success');
                $log.debug(response);

                vm.loginErrorMsg = undefined;
                var token = response.data.auth_access_token;

                $log.debug('token = ' + token);

                localStorageService.set('username', vm.credentials.username);
                authPrinciple.authenticate(token);

                if (angular.isDefined($rootScope.toState)) {
                    if ($rootScope.toState.name == 'login') {
                        if (angular.isDefined($rootScope.returnToState)) {
                            if ($rootScope.returnToState.name != 'login') {
                                $state.go($rootScope.returnToState.name, $rootScope.returnToStateParams);
                            } else {
                                $state.go('profile');
                            }
                        } else {
                            $state.go('profile');
                        }
                    } else {
                        $state.go($rootScope.toState.name, $rootScope.returnToStateParams);
                    }
                }

            }, function(response) {
                $log.debug('login error');
                $log.debug(response);

                if (response.status === 401) {
                    vm.loginErrorMsg = "帳號或密碼錯誤";
                }
            });
        }
    }
})();
(function() {
    'use strict';

    angular
        .module('vmsFrontend')
        .controller('EmailVerificationController', EmailVerificationController);

    /** @ngInject */
    function EmailVerificationController($log, $location, vmsClient, authPrinciple, $state, $rootScope, $timeout) {
        var vm = this;
        vm.verification = true;
        vm.verificationSuccess = false;
        vm.verificationStatus = vm.verification || vm.verificationSuccess;

        $log.debug("vm.verification " + vm.verification);
        $log.debug("vm.verificationSuccess " + vm.verificationSuccess);
        $log.debug("vm.verificationStatus " + vm.verificationStatus);

        vm.volunteer = $location.search();

        if (angular.isDefined(vm.volunteer.email) && angular.isDefined(vm.volunteer.verification_token)) {

            angular.element(document).ready(verify());
        } else {
            $log.debug("verification false");
            vm.verification = false;
            vm.verificationStatus = vm.verification || vm.verificationSuccess;

            $log.debug("vm.verificationStatus " + vm.verificationStatus);
        }

        function verify() {
            $log.debug("EmailVerificationController verify()");

            $log.debug("email = " + vm.volunteer.email);
            $log.debug("verification_token = " + vm.volunteer.verification_token);

            vmsClient.emailVerification(vm.volunteer.email, vm.volunteer.verification_token, function(response) {
                $log.debug('emailVerification success');
                $log.debug(response);

                vm.verification = false;
                vm.verificationSuccess = true;
                vm.verificationStatus = vm.verification || vm.verificationSuccess;

                $timeout(function() {
                    $state.go('profile')
                }, 5000);
            }, function(response) {
                $log.debug('emailVerification error');
                $log.debug(response);

                vm.verification = false;
                vm.verificationStatus = vm.verification || vm.verificationSuccess;
            });
        }
    }
})();
