$templateCache.put("app/registerPrivacy/registerPrivacy.html","<div class=\"col-lg-offset-2 col-lg-8 col-sm-12 col-xs-12\"><h1>隱私權政策</h1><div id=\"main-content\"><p class=\"lead\">歡迎您使用 VMS 網站（下稱「本網站」）與相關服務，為了尊重與保護您在本網站上個人資料的隱密性，VMS 將透過這份「VMS 隱私權聲明」（下稱「本聲明」），讓您瞭解VMS 如何透過本網站蒐集、運用與保護您個人的非公示資訊。請您詳細閱讀本聲明，如有任何疑問歡迎您透過 e-mail 與 VMS 聯絡： volunteer.service.tw@gmail.com。</p><p class=\"text-right\">更新日期：2015/10/27</p><hr><h3>個人資料的蒐集方式</h3><p>VMS 透過本網站蒐集個人資料之方式包括：<ol><li>要求您自行輸入個人資料；</li><li>系統自動記錄您瀏覽本網站之歷程</li></ol></p><h3>蒐集的個人資料內容</h3><p>您在本網站註冊新帳號並完成註冊程序後，即成為本網站會員，而您在註冊時需提供的個人資料包括帳號名稱、您的姓名、e-mail 與密碼及其他聯絡方式等。</p><p>註冊完成之後，您可以到「我的資料」填寫、編輯您的個人資訊。您可以自由決定是否填寫這些個人資訊，「我的資料」中的個人資訊並非必填項目。</p><p>若您有加入的外包專案，VMS 將會紀錄您所參與之專案。您亦得在本網站的專案中提供您的個人資料。</p><p>當您瀏覽本網站時，VMS 將以匿名方式蒐集您的操作流程，包括您使用連線設備的IP位址、使用時間、使用的瀏覽器、瀏覽及點選資料紀錄。</p><h3>個人資料之運用</h3><p>為執行本網站相關規定，VMS 可能透過您提供的個人資料與您進行聯絡。</p><p>您的帳號名稱將可讓外包專案中的專案管理員查看，並詢問您是否同意參加專案，除此之外，只要沒有自行將您其他的註冊資料揭露於本網站上，任何使用者均無法瀏覽或利用您的資料。</p><h3>個人聯絡資料之運用</h3><p>本網站將透過電子郵件傳送本網站相關資訊，這些資訊包括但不限於使用者註冊確認信、本聲明與使用條款的修正訊息以、專案邀請、通知信件及本網站活動資訊，但您的電子信箱不會在其他使用者之收件者列表中顯示。</p><h3>線上活動紀錄的運用</h3><p>當您瀏覽本網站，伺服器會自動記錄使用者機器的 IP 位址、使用時間、使用的瀏覽器、瀏覽及點選紀錄等資料，並加以分析。</p><p>本網站僅會針對全體使用者行為之總和進行分析，不會針對您個別之行為進行分析，除非您願意告知您的個人資料，否則 VMS 並不會將此項記錄和您對應。</p><h3>向第三方揭露個人資料</h3><p>若為中華民國相關法令、司法或政府機關要求，或經您的同意，本網站將會揭露您的個人資料，但僅限於向相關之第三人揭露您的個人資料。</p><h3>個人資料之修改</h3><p>當您在本網站註冊後，您可以隨時利用我的資料中之連結，更改（目前尚未提供）、刪除帳號以外您所輸入的任何資料，例如個人姓名、電子信箱、聯絡方式等。 但除了您主動登錄的個人資料之外，您在本網站相關外包專案或事件追蹤系統中所提供之個人資料，不在此修改範圍之內。</p><h3>資料保護措施</h3><p>本網站將會運用安全技術和程序來保護您的個人資料，避免未經您授權的存取、使用或洩露。例如，本網站會將您提供的個人資訊儲存在嚴密控管的設施與電腦設備中，並僅允許少數系統管理者擁有存取權限。</p><p>請妥善保管您的密碼及個人資料，請勿將任何個人資料，尤其是您的密碼提供給任何人，若因此造成任何損失，本不負任何責任。</p><h3>造訪其他網站之注意事項</h3><p>透過本網站所提供的超連結，您可能造訪與本網站相連結之其他網站，這些其他網站也可能蒐集您的個人資料。對於您主動提供的個人資訊，各相連結之其他網站有其個別的隱私權保護政策，其資料處理措施不適用本聲明，本不負任何責任，建議您詳加閱讀這些網站的隱私權保護政策。</p><h3>隱私權聲明更新</h3><p>本網站將不定期更新本聲明，更新時，本網站會同時修訂本聲明的「最後更新日期」。本聲明的重要變更，亦會張貼在本網站上顯著位置，以提醒您注意。</p><hr><button class=\"btn btn-primary btn-lg\" ui-sref=\"register\">同意</button> <button class=\"btn btn-default btn-lg\">不同意</button></div></div>");
